import { useState, useEffect } from "react";
import { ethers } from "ethers";

// Components
import Navigation from "../components/Navigation";
import Search from "../components/Search";
import Detail from "../components/Detail";
import AddProperty from "../components/AddProperty";
import RentalProperty from "../../src/artifacts/contracts/RentalProperty.sol/RentalProperty.json"; //  RentalProperty  ABI  bytecode
import RentalEscrow from "../../src/artifacts/contracts/RentalEscrow.sol/RentalEscrow.json"; //  RentalEscrow  ABI  bytecode

// ABIs
import deploy, {
  RentalPropertyArray,
  RentalEscrowArray,
  metadataArrary,
  tokenId,
} from "../scripts/deploy";
import {
  PRIVATE_KEY0,
  PRIVATE_KEY1,
  PRIVATE_KEY2,
  RPC_URL,
} from "../components/accountSetting";
function Home({ account, setAccount }) {
  const [loading, setloading] = useState(false);
  const [provider, setProvider] = useState(null);
  const [properties, setProperties] = useState([]);
  const [property, setSelectedProperty] = useState({});
  const [toggle, setToggle] = useState(false); // rentProperty detail 
  const [addtoggle, setAddtoggle] = useState(false); //add rentProperty 
  const tokenId = 173;

  // 
  async function deployOnclick() {
    await deploy();
    await loadBlockchainDataOnclick();
  }

  async function loadBlockchainData() {
    setloading(true);
    try {
      // Web3Provider
      const provider = new ethers.providers.JsonRpcProvider(RPC_URL);
      setProvider(provider);
      var EscrowArray = [],
        PropertyArray = [];
      // 
      var properties = [];

      //sepolia
      // const rentalEscrowAddress = [
      //   "0xA96b1EC160fa9DEbFfb358636D46F66cf9e488E5",
      //   "0xbe23AD14765b5DC441827C8a49cE1E5B5C4b4225",
      //   "0x0003910e0E0ABE313e5C8FF870CBeC04A9197C6e",
      // ];
      const rentalEscrowAddress = [
        "0xe7f1725E7734CE288F8367e1Bb143E90bb3F0512",
        "0xCf7Ed3AccA5a467e9e704C703E8D87F634fB0Fc9",
        "0x5FC8d32690cc91D4c39d9d3abcBD16989F875707",
      ];

      for (let i = 0; i < 3; i++) {
        // 
        const rentalEscrow = new ethers.Contract(
          rentalEscrowAddress[i],
          RentalEscrow.abi,
          provider
        );

        const rentalPropertyAddress = await rentalEscrow.rentalProperty();

        // ;
        const rentalProperty = new ethers.Contract(
          rentalPropertyAddress,
          RentalProperty.abi,
          provider
        );
        EscrowArray[i] = rentalEscrow;
        PropertyArray[i] = rentalProperty;
      }
      // hardhat
      // EscrowArray = RentalEscrowArray;
      // PropertyArray = RentalPropertyArray;

      // 
      for (let i = 0; i < 3; i++) {
        //   
        const rentalEscrow = EscrowArray[i];
        const rentalProperty = PropertyArray[i];

        //  URI 
        const uri = await rentalProperty.tokenURI(tokenId);
        const response = await fetch(uri);
        const metadata = await response.json();
        //  RentalEscrow 

        // propertyInfo -> [landlord, isAvailable, rentPrice, securityDeposit, tenant]
        const [landlord, isAvailable, rentPrice, securityDeposit, tenant] =
          await rentalProperty.getPropertyInfo(tokenId);
        let isRented = await rentalProperty.isRented(tokenId);

        //  properties 
        properties.push({
          landlord: landlord, //
          isAvailable: isAvailable, // 
          rentPrice: ethers.utils.formatEther(rentPrice), // 
          securityDeposit: ethers.utils.formatEther(securityDeposit), // 
          id: tokenId, // 
          rentalProperty: rentalProperty,
          rentalEscrow: rentalEscrow,
          rentalPropertyAddress: rentalProperty.address, // 
          rentalEscrowAddress: rentalEscrow.address, // 
          isRented: isRented,
          name: metadata.name, // 
          tenant: tenant, // 
          description: metadata.description, // 
          image: metadata.image, // 
          attributes: metadata.attributes, //
        });

        console.log(` ${i + 1} `);
      }

      // 
      setProperties(properties);
      console.log("");

      // 
      const accounts = await window.ethereum.request({
        method: "eth_requestAccounts", // 
      });
      const account = ethers.utils.getAddress(accounts[0]); // 
      setAccount(account); // 

      // 
      window.ethereum.on("accountsChanged", async () => {
        const accounts = await window.ethereum.request({
          method: "eth_requestAccounts", // 
        });
        const account = ethers.utils.getAddress(accounts[0]); // 
        setAccount(account); // 
      });
    } finally {
      setloading(false);
    }
    // } catch (error) {
    //   // 
    //   console.error("Error loading blockchain data:", error);
    // }
  }

  // 
  const togglePop = (property) => {
    setSelectedProperty(property); // 
    setToggle(!toggle); // /
  };
  const addtogglePop = () => {
    setAddtoggle(!addtoggle); // /
  };
  // 
  async function loadBlockchainDataOnclick() {
    loadBlockchainData(); // 
  }
  // 
  useEffect(() => {
    console.log("Home Page useEffect()");

    loadBlockchainData();
  }, []); // 

  return (
    <div className="home_overlay">
      <div style={{ height: "100vh", overflowY: "auto" }}>
        <div className="home">
          <Navigation account={account} setAccount={setAccount} />
          <Search />
          <div className="home_container">
            <div className="home_container_title">
              <h2> Properties For Rent </h2>
              {/* <button onClick={deployOnclick}>deploy</button> */}
              {/* <button onClick={loadBlockchainDataOnclick}>refresh</button> */}
              <div className="home_addProperty_Button">
                <button onClick={() => addtogglePop()}>add</button>
              </div>
            </div>
            <div className="home_container_cards">
              {loading && (
                <div className="loading_overlay">
                  <div className="loading_spinner">Loading, please wait...</div>
                </div>
              )}
              <div className="cards">
                {properties.map((property, index) => (
                  <div
                    className="card"
                    key={index}
                    onClick={() => togglePop(property)}
                  >
                    <div className="card__image">
                      <img src={property.image} alt="Property" />
                    </div>
                    <div className="card__info">
                      <h4>{property.name}</h4>
                      <p>
                        <strong>{property.rentPrice}</strong> ETH/month |
                        <strong>{property.securityDeposit}</strong> ETH deposit
                      </p>
                      {property.attributes && (
                        <p>
                          <strong>{property.attributes[6]?.value}</strong> bds |
                          <strong>{property.attributes[7]?.value}</strong> ba |
                          <strong>{property.attributes[4]?.value}</strong> sqft
                        </p>
                      )}
                      <p className="availability">
                        {property.isRented ? " Rented" : " Available"}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>{" "}
            {toggle && (
              <Detail
                property={property}
                togglePop={togglePop}
                loadBlockchainData={loadBlockchainData}
                tokenId={tokenId}
              />
            )}
            {addtoggle && (
              <AddProperty
                addtogglePop={addtogglePop}
                loadBlockchainData={loadBlockchainData}
                tokenId={tokenId}
              />
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
